.content-container {
  position: relative;
  width: 100%;
  height: 95vh;
  overflow: hidden;
}

.content-container .image-component {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: 100% auto;
  background-position: center;
  background-repeat: no-repeat;
  box-sizing: border-box; /* Add this line */
  max-width: 100%;
  max-height: 95vh;
  transition: opacity 0.5s ease; /* Add transition */
}

.content-container .text-overlay {
  font-family: 'Roboto', sans-serif; /* Use Roboto font */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #1D1D1D; /* Adjust the text color */
  font-size: 24px; /* Adjust the font size */
  text-align: center;
  background-color: rgba(255, 255, 255, 0.5);
  max-width: 100%;
  padding-left: 50%;
  padding-right: 50%;
  white-space: nowrap;
}

.blur-image-component {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: 100% auto;
  background-position: center;
  background-repeat: no-repeat;
  box-sizing: border-box;
  max-width: 100%;
  max-height: 95vh;
  opacity: 1;
  transition: opacity 0.5s ease; /* Add transition */
}
